import http from './service'

//  --- BEGIN
// 
export function getProductList(data) {
    let url = '/api/Product'

    return http.get(url, data)
}
// 
export function getProductCatelog(data) {
    let url = '/api/ProductCatalog'

    return http.get(url, data)
}
//  --- END


//  --- BEGIN
// 
export function getMerchantOrder(data) {
    let url = '/api/MerchantOrder'

    return http.get(url, data)
}
// 
export function getMerchantOrderDetail(id) {
    let url = `/api/MerchantOrder/${id}`

    return http.get(url)
}
// 
export function merchantOrderConfirm(data) {
    let url = '/api/MerchantOrderConfirm'

    return http.post(url, data)
}
//  --- END


//  --- BEGIN
// 
export function getShoppingCart(data) {
    let url = '/api/ShoppingCart'

    return http.get(url, data)
}
//
export function addOrUpdateShoppingCart(data) {
    let url = '/api/ShoppingCart'

    return http.put(url, data)
}
// 
export function deleteShoppingCart(id) {
    let url = `/api/ShoppingCart/${id}`

    return http.delete(url)
}
// 
export function confirmShoppingCart(data) {
    let url = '/api/MerchantOrder'

    return http.post(url, data)
}
//  --- END


//  --- BEGIN
// 
export function getMerchantDeliveryOrder(data) {
    let url = '/api/MerchantDeliveryOrder'

    return http.get(url, data)
}
// 
export function getMerchantDeliveryOrderDetail(id) {
    let url = `/api/MerchantDeliveryOrder/${id}`

    return http.get(url)
}
// 
export function merchantDeliveryOrderConfirm(data) {
    let url = '/api/MerchantDeliveryOrderConfirm'

    return http.post(url, data)
}
//  --- END


//  --- BEGIN
// 
export function getMerchantInventory(data) {
    let url = '/api/MerchantInventory'

    return http.get(url, data)
}
// 
export function  updateProductCatelog(id) {
    let url = `/api/MerchantInventory/${id}`

    return http.put(url)
}
//  --- END


//  --- BEGIN
// 
export function getAddress(data) {
    let url = '/api/MerchantShippingAddress'

    return http.get(url, data)
}
// 
export function addAddresss(data) {
    let url = '/api/MerchantShippingAddress'

    return http.post(url, data)
}
// 
export function updateAddress(data) {
    let url = '/api/MerchantShippingAddress'

    return http.put(url, data)
}
// 
export function deleteAddress(id) {
    let url = `/api/MerchantShippingAddress/${id}`

    return http.delete(url)
}
//  --- END



