import axios from 'axios'
import { ORIGIN,NOT_LOGIN,ERR_CODE } from './config'
import { getToken } from 'common/js/cache'
import { isHttp } from 'common/js/util'
import _this from '../main'

const Authorization = getToken()

// ORIGIN
const currentOrigin = window.location.origin

// 
function resolve(res) {
    if(res.data && res.data.errCode === NOT_LOGIN){
        window.location.href = currentOrigin
    } else {
        if(res.data && res.data.errCode !== ERR_CODE){
            _this.$alert.show({'message': res.data.msg})
        }
        return Promise.resolve(res.data)
    }
}

// 204200
// https://segmentfault.com/q/1010000013302914/a-1020000013338290
const http = {
    get:(url,params) => {
        if (!isHttp(url)){
            url = ORIGIN + url
        }
        return axios.get(url, {
            headers: {
                Authorization
            },
            params
        }).then(resolve)
    },
    post:(url,params) => {
        if (!isHttp(url)) {
            url = ORIGIN + url
        }
        return axios.post(url, params, {
            headers: {
                Authorization
            }
        }).then(resolve)
    },
    put:(url,params) => {
        if (!isHttp(url)) {
            url = ORIGIN + url
        }
        return axios.put(url, params, {
            headers: {
                Authorization
            }
        }).then(resolve)
    },
    delete:(url,params) => {
        if (!isHttp(url)){
            url = ORIGIN + url
        }
        return axios.delete(url, {
            headers: {
                Authorization
            },
            params
        }).then(resolve)
    }
}

export default http


