import http from './service'

// 
// 
export function getMemberOrder(data) {
    let url = '/api/MemberOrder'

    return http.get(url, data)
}

// 
export function getMemberUser(data) {
    let url = '/api/MemberUser'

    return http.get(url, data)
}

// 
export function cancelMemberOrder(data) {
    let url = '/api/MemberOrderCancel'

    return http.post(url, data)
}

// 
export function getMerchantBill(data) {
    let url = '/api/MerchantBill'

    return http.get(url, data)
}

// 
export function getMerchantBillDetail(id) {
    let url = `/api/MerchantBill/${id}`

    return http.get(url)
}

// 
export function getMerchantSettlementOrder(data) {
    let url = '/api/MerchantSettlementOrder'

    return http.get(url, data)
}


